import { motion, AnimatePresence } from 'framer-motion'
import { X, Plus, Minus, Trash2, ShoppingBag, ArrowLeft } from 'lucide-react'
import { Link } from 'react-router-dom'

// Store
import { useCartStore, formatPrice } from '@/stores/cartStore'

const CartSidebar = () => {
  const { 
    items, 
    isOpen, 
    closeCart, 
    removeItem, 
    updateQuantity, 
    totalPrice, 
    totalItems,
    clearCart 
  } = useCartStore()

  const handleQuantityChange = (gameId: string, newQuantity: number) => {
    if (newQuantity < 1) {
      removeItem(gameId)
    } else {
      updateQuantity(gameId, newQuantity)
    }
  }

  if (items.length === 0) {
    return (
      <AnimatePresence>
        {isOpen && (
          <>
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="fixed inset-0 bg-black/50 z-30"
              onClick={closeCart}
            />
            
            <motion.div
              initial={{ x: '100%' }}
              animate={{ x: 0 }}
              exit={{ x: '100%' }}
              transition={{ type: 'spring', damping: 25, stiffness: 200 }}
              className="fixed top-0 left-0 w-full h-full bg-bg-secondary border-l border-bg-tertiary z-40 flex flex-col"
            >
              {/* Header */}
              <div className="flex items-center justify-between p-6 border-b border-bg-tertiary">
                <h2 className="text-xl font-display font-bold text-text-primary">
                   
                </h2>
                <button
                  onClick={closeCart}
                  className="p-2 text-text-secondary hover:text-text-primary hover:bg-bg-tertiary rounded-lg transition-colors"
                >
                  <X size={20} />
                </button>
              </div>

              {/* Empty State */}
              <div className="flex-1 flex items-center justify-center p-6">
                <div className="text-center">
                  <ShoppingBag size={64} className="text-text-muted mx-auto mb-4" />
                  <h3 className="text-lg font-semibold text-text-primary mb-2">
                        
                  </h3>
                  <p className="text-text-secondary mb-6">
                         
                  </p>
                  <Link
                    to="/store"
                    onClick={closeCart}
                    className="btn-primary"
                  >
                     
                  </Link>
                </div>
              </div>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    )
  }

  return (
    <AnimatePresence>
      {isOpen && (
        <>
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 bg-black/50 z-30"
            onClick={closeCart}
          />
          
          <motion.div
            initial={{ x: '100%' }}
            animate={{ x: 0 }}
            exit={{ x: '100%' }}
            transition={{ type: 'spring', damping: 25, stiffness: 200 }}
            className="fixed top-0 left-0 w-full md:w-96 h-full bg-bg-secondary border-l border-bg-tertiary z-40 flex flex-col"
          >
            {/* Header */}
            <div className="flex items-center justify-between p-6 border-b border-bg-tertiary">
              <div>
                <h2 className="text-xl font-display font-bold text-text-primary">
                   
                </h2>
                <p className="text-sm text-text-secondary">
                  {totalItems} 
                </p>
              </div>
              <button
                onClick={closeCart}
                className="p-2 text-text-secondary hover:text-text-primary hover:bg-bg-tertiary rounded-lg transition-colors"
              >
                <X size={20} />
              </button>
            </div>

            {/* Cart Items */}
            <div className="flex-1 overflow-y-auto p-6 space-y-4">
              {items.map((item) => (
                <motion.div
                  key={item.game.id}
                  layout
                  initial={{ opacity: 0, y: 20 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -20 }}
                  className="flex gap-4 p-4 bg-bg-tertiary rounded-lg"
                >
                  <img
                    src={item.game.image}
                    alt={item.game.title}
                    className="w-16 h-20 object-cover rounded"
                  />
                  
                  <div className="flex-1 min-w-0">
                    <h4 className="text-text-primary font-medium mb-1 truncate">
                      {item.game.title}
                    </h4>
                    <p className="text-text-secondary text-sm mb-2">
                      {item.game.genre}
                    </p>
                    
                    <div className="flex items-center justify-between">
                      <span className="text-primary font-bold">
                        {formatPrice(item.game.price)}
                      </span>
                      
                      <div className="flex items-center gap-2">
                        <button
                          onClick={() => handleQuantityChange(item.game.id, item.quantity - 1)}
                          className="w-8 h-8 rounded-full bg-bg-secondary flex items-center justify-center text-text-secondary hover:text-text-primary hover:bg-bg-primary transition-colors"
                        >
                          <Minus size={14} />
                        </button>
                        
                        <span className="w-8 text-center text-text-primary font-medium">
                          {item.quantity}
                        </span>
                        
                        <button
                          onClick={() => handleQuantityChange(item.game.id, item.quantity + 1)}
                          className="w-8 h-8 rounded-full bg-bg-secondary flex items-center justify-center text-text-secondary hover:text-text-primary hover:bg-bg-primary transition-colors"
                        >
                          <Plus size={14} />
                        </button>
                      </div>
                    </div>
                    
                    <div className="flex items-center justify-between mt-2">
                      <span className="text-sm text-text-muted">
                        {formatPrice(item.game.price * item.quantity)}
                      </span>
                      
                      <button
                        onClick={() => removeItem(item.game.id)}
                        className="text-status-error hover:bg-status-error/10 p-1 rounded transition-colors"
                      >
                        <Trash2 size={14} />
                      </button>
                    </div>
                  </div>
                </motion.div>
              ))}
            </div>

            {/* Cart Summary & Actions */}
            <div className="border-t border-bg-tertiary p-6 space-y-4">
              
              {/* Clear Cart */}
              {items.length > 1 && (
                <button
                  onClick={clearCart}
                  className="w-full text-center text-status-error hover:bg-status-error/10 py-2 rounded-lg transition-colors text-sm"
                >
                    
                </button>
              )}

              {/* Summary */}
              <div className="space-y-2">
                <div className="flex justify-between text-text-secondary">
                  <span>  ({totalItems} )</span>
                  <span>{formatPrice(totalPrice)}</span>
                </div>
                
                <div className="flex justify-between text-text-secondary">
                  <span></span>
                  <span className="text-status-success">- </span>
                </div>
                
                <div className="border-t border-bg-tertiary pt-2">
                  <div className="flex justify-between text-lg font-bold text-text-primary">
                    <span>  </span>
                    <span className="text-primary">{formatPrice(totalPrice)}</span>
                  </div>
                </div>
              </div>

              {/* Actions */}
              <div className="space-y-3">
                <Link
                  to="/checkout"
                  onClick={closeCart}
                  className="btn-primary w-full justify-center"
                >
                  
                  <ArrowLeft size={18} />
                </Link>
                
                <Link
                  to="/store"
                  onClick={closeCart}
                  className="btn-secondary w-full justify-center"
                >
                   
                </Link>
              </div>

              {/* Trust Indicators */}
              <div className="flex items-center justify-center gap-4 text-xs text-text-muted pt-4">
                <div className="flex items-center gap-1">
                  <span></span>
                  <span> </span>
                </div>
                <div className="flex items-center gap-1">
                  <span></span>
                  <span> </span>
                </div>
                <div className="flex items-center gap-1">
                  <span></span>
                  <span></span>
                </div>
              </div>
            </div>
          </motion.div>
        </>
      )}
    </AnimatePresence>
  )
}

export default CartSidebar