import { Routes, Route } from 'react-router-dom'
import { useEffect } from 'react'
import { AnimatePresence } from 'framer-motion'

// Layout Components
import Layout from '@/components/layout/Layout'
import LoadingScreen from '@/components/ui/LoadingScreen'

// Conditional Performance Monitor (Development Only)
const PerformanceMonitor = typeof window !== 'undefined' && window.location.hostname === 'localhost'
  ? require('@/components/ui/PerformanceMonitor').default 
  : () => null

// Lazy-loaded pages for better performance
import { LazyPages } from '@/components/ui/LazyPages'

// Hooks
import { useAuthStore } from '@/stores/authStore'
import { useCartStore } from '@/stores/cartStore'



function App() {
  const { initializeAuth } = useAuthStore()
  const { initializeCart } = useCartStore()

  useEffect(() => {
    // Initialize stores on app load
    initializeAuth()
    initializeCart()
  }, [initializeAuth, initializeCart])

  return (
    <div className="min-h-screen bg-bg-primary">
      <LoadingScreen />
      
      <AnimatePresence mode="wait">
        <Routes>
          <Route path="/" element={<Layout />}>
            <Route index element={<LazyPages.Home />} />
            
            <Route path="/store" element={<LazyPages.Store />} />
            
            <Route path="/game/:id" element={<LazyPages.GameDetails />} />
            
            <Route path="/cart" element={<LazyPages.Cart />} />
            
            <Route path="/checkout" element={<LazyPages.Checkout />} />
            
            <Route path="/library" element={<LazyPages.Library />} />
            
            <Route path="/about" element={<LazyPages.About />} />
            
            <Route path="/support" element={<LazyPages.Support />} />
            
            <Route path="*" element={<LazyPages.NotFound />} />
          </Route>
          
          {/* Auth Routes (no layout) */}
          <Route path="/login" element={<LazyPages.Login />} />
          
          <Route path="/register" element={<LazyPages.Register />} />
          
          {/* Protected Routes */}
          <Route path="/profile" element={<LazyPages.Profile />} />
        </Routes>
      </AnimatePresence>
      
      {/* Development Performance Monitor (Hidden in Production) */}
      {typeof window !== 'undefined' && window.location.hostname === 'localhost' && <PerformanceMonitor />}
    </div>
  )
}

export default App