import { lazy, Suspense } from 'react'
import { motion } from 'framer-motion'
import LoadingScreen from './LoadingScreen'

// Lazy load pages for better performance
const Home = lazy(() => import('../../pages/Home'))
const Store = lazy(() => import('../../pages/Store'))
const GameDetails = lazy(() => import('../../pages/GameDetails'))

// Placeholder pages (keep them simple for now)
const Cart = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.Cart })))
const Checkout = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.Checkout })))
const Login = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.Login })))
const Register = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.Register })))
const Profile = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.Profile })))
const Library = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.Library })))
const About = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.About })))
const Support = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.Support })))
const NotFound = lazy(() => import('../../pages/PlaceholderPages').then(module => ({ default: module.NotFound })))

// Page loading wrapper with enhanced animations
const PageLoader = ({ children }: { children: React.ReactNode }) => (
  <motion.div
    initial={{ opacity: 0, y: 20 }}
    animate={{ opacity: 1, y: 0 }}
    exit={{ opacity: 0, y: -20 }}
    transition={{ duration: 0.3, ease: "easeOut" }}
  >
    {children}
  </motion.div>
)

// Enhanced Suspense wrapper with better UX
const EnhancedSuspense = ({ children, fallback }: { children: React.ReactNode, fallback?: React.ReactNode }) => (
  <Suspense 
    fallback={
      fallback || (
        <div className="min-h-screen bg-bg-primary flex items-center justify-center">
          <LoadingScreen />
        </div>
      )
    }
  >
    {children}
  </Suspense>
)

// Export lazy components with suspense wrapper
export const LazyPages = {
  Home: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Home {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  Store: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Store {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  GameDetails: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <GameDetails {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  Cart: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Cart {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  Checkout: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Checkout {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  Login: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Login {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  Register: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Register {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  Profile: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Profile {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  Library: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Library {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  About: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <About {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  Support: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <Support {...props} />
      </PageLoader>
    </EnhancedSuspense>
  ),
  
  NotFound: (props: any) => (
    <EnhancedSuspense>
      <PageLoader>
        <NotFound {...props} />
      </PageLoader>
    </EnhancedSuspense>
  )
}

// Export individual lazy components
export {
  Home,
  Store,
  GameDetails,
  Cart,
  Checkout,
  Login,
  Register,
  Profile,
  Library,
  About,
  Support,
  NotFound
}

export default LazyPages