import { useEffect, useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { 
  Gamepad2, 
  Zap, 
  Sparkles, 

  Star,
  TrendingUp
} from 'lucide-react'

interface LoadingScreenProps {
  duration?: number
  showProgress?: boolean
  message?: string
}

const LoadingScreen = ({ 
  duration = 2500, 
  showProgress = true,

}: LoadingScreenProps) => {
  const [isVisible, setIsVisible] = useState(true)
  const [progress, setProgress] = useState(0)
  const [currentMessage, setCurrentMessage] = useState(0)

  const loadingMessages = [
    '    ...',
    '  ...',
    '  ...',
    '  ...',
    '  !'
  ]

  useEffect(() => {
    const progressTimer = setInterval(() => {
      setProgress(prev => {
        const newProgress = prev + (100 / (duration / 50))
        if (newProgress >= 100) {
          clearInterval(progressTimer)
          return 100
        }
        return newProgress
      })
    }, 50)

    const messageTimer = setInterval(() => {
      setCurrentMessage(prev => (prev + 1) % loadingMessages.length)
    }, duration / loadingMessages.length)

    const hideTimer = setTimeout(() => {
      setIsVisible(false)
    }, duration)

    return () => {
      clearInterval(progressTimer)
      clearInterval(messageTimer)
      clearTimeout(hideTimer)
    }
  }, [duration])

  if (!isVisible) return null

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        transition={{ duration: 0.8, ease: "easeInOut" }}
        className="fixed inset-0 z-50 bg-bg-primary overflow-hidden"
      >
        {/* Animated Background */}
        <div className="absolute inset-0">
          {/* Gradient Background */}
          <div className="absolute inset-0 bg-gradient-to-br from-bg-primary via-bg-secondary to-bg-primary" />
          
          {/* Animated Shapes */}
          <div className="absolute inset-0">
            {/* Floating Orbs */}
            <motion.div
              className="absolute top-1/4 left-1/4 w-64 h-64 bg-primary/10 rounded-full blur-3xl"
              animate={{
                scale: [1, 1.2, 1],
                opacity: [0.3, 0.6, 0.3],
                x: [0, 50, 0],
                y: [0, -30, 0]
              }}
              transition={{
                duration: 8,
                repeat: Infinity,
                ease: "easeInOut"
              }}
            />
            <motion.div
              className="absolute top-3/4 right-1/4 w-48 h-48 bg-accent/10 rounded-full blur-3xl"
              animate={{
                scale: [1.2, 1, 1.2],
                opacity: [0.4, 0.2, 0.4],
                x: [0, -40, 0],
                y: [0, 20, 0]
              }}
              transition={{
                duration: 6,
                repeat: Infinity,
                ease: "easeInOut",
                delay: 2
              }}
            />
            <motion.div
              className="absolute top-1/2 left-1/2 w-32 h-32 bg-status-success/10 rounded-full blur-3xl"
              animate={{
                scale: [1, 1.5, 1],
                opacity: [0.2, 0.5, 0.2],
                rotate: [0, 180, 360]
              }}
              transition={{
                duration: 10,
                repeat: Infinity,
                ease: "linear"
              }}
            />
          </div>
          
          {/* Grid Pattern */}
          <div className="absolute inset-0 opacity-5">
            <div 
              className="w-full h-full"
              style={{
                backgroundImage: `
                  linear-gradient(rgba(108, 92, 231, 0.3) 1px, transparent 1px),
                  linear-gradient(90deg, rgba(108, 92, 231, 0.3) 1px, transparent 1px)
                `,
                backgroundSize: '50px 50px'
              }}
            />
          </div>
        </div>

        {/* Main Content */}
        <div className="relative z-10 h-full flex items-center justify-center">
          <div className="text-center max-w-md mx-auto px-6">
            
            {/* Logo Container */}
            <motion.div
              initial={{ scale: 0, rotate: -180, opacity: 0 }}
              animate={{ scale: 1, rotate: 0, opacity: 1 }}
              transition={{ 
                duration: 1, 
                ease: "easeOut",
                type: "spring",
                stiffness: 200,
                damping: 20
              }}
              className="mb-12"
            >
              {/* Logo with Pulse Effect */}
              <div className="relative mx-auto w-24 h-24 mb-6">
                {/* Outer Glow Ring */}
                <motion.div
                  className="absolute inset-0 rounded-full bg-gradient-primary"
                  animate={{
                    scale: [1, 1.2, 1],
                    opacity: [0.8, 0.3, 0.8]
                  }}
                  transition={{
                    duration: 2,
                    repeat: Infinity,
                    ease: "easeInOut"
                  }}
                />
                
                {/* Main Logo */}
                <motion.div
                  className="relative w-full h-full bg-bg-secondary rounded-full flex items-center justify-center border-2 border-primary/50 backdrop-blur-sm"
                  whileHover={{ scale: 1.05 }}
                  transition={{ type: "spring", stiffness: 400, damping: 17 }}
                >
                  <Gamepad2 size={40} className="text-primary" />
                  
                  {/* Floating Icons */}
                  <motion.div
                    className="absolute -top-2 -right-2"
                    animate={{
                      y: [0, -5, 0],
                      rotate: [0, 10, 0]
                    }}
                    transition={{
                      duration: 2,
                      repeat: Infinity,
                      ease: "easeInOut"
                    }}
                  >
                    <Sparkles size={16} className="text-accent" />
                  </motion.div>
                  
                  <motion.div
                    className="absolute -bottom-2 -left-2"
                    animate={{
                      y: [0, 5, 0],
                      rotate: [0, -10, 0]
                    }}
                    transition={{
                      duration: 2.5,
                      repeat: Infinity,
                      ease: "easeInOut",
                      delay: 0.5
                    }}
                  >
                    <Star size={14} className="text-yellow-400" />
                  </motion.div>
                </motion.div>
              </div>
              
              {/* Brand Name */}
              <motion.h1
                initial={{ y: 20, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.5, duration: 0.6 }}
                className="text-4xl font-display font-bold bg-gradient-primary bg-clip-text text-transparent mb-2"
              >
                GameStore
              </motion.h1>
              
              <motion.p
                initial={{ y: 20, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.7, duration: 0.6 }}
                className="text-text-secondary text-lg"
              >
                  
              </motion.p>
            </motion.div>

            {/* Loading Animation */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: 1, duration: 0.6 }}
              className="space-y-6"
            >
              {/* Custom Spinner */}
              <div className="relative">
                <motion.div
                  className="w-16 h-16 mx-auto"
                  animate={{ rotate: 360 }}
                  transition={{ duration: 2, repeat: Infinity, ease: "linear" }}
                >
                  <div className="w-full h-full border-4 border-bg-tertiary border-t-primary rounded-full" />
                </motion.div>
                
                {/* Inner Pulse */}
                <motion.div
                  className="absolute inset-0 flex items-center justify-center"
                  animate={{ scale: [1, 1.1, 1] }}
                  transition={{ duration: 1.5, repeat: Infinity, ease: "easeInOut" }}
                >
                  <Zap size={20} className="text-accent" />
                </motion.div>
              </div>

              {/* Dynamic Message */}
              <motion.div
                key={currentMessage}
                initial={{ opacity: 0, y: 10 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -10 }}
                transition={{ duration: 0.5 }}
                className="h-6"
              >
                <p className="text-text-secondary text-lg">
                  {loadingMessages[currentMessage]}
                </p>
              </motion.div>
            </motion.div>

            {/* Progress Bar */}
            {showProgress && (
              <motion.div
                initial={{ opacity: 0, scaleX: 0 }}
                animate={{ opacity: 1, scaleX: 1 }}
                transition={{ delay: 1.2, duration: 0.6 }}
                className="mt-8"
              >
                <div className="w-full h-2 bg-bg-tertiary rounded-full overflow-hidden">
                  <motion.div
                    className="h-full bg-gradient-primary rounded-full"
                    initial={{ width: 0 }}
                    animate={{ width: `${progress}%` }}
                    transition={{ duration: 0.1, ease: "easeOut" }}
                  />
                </div>
                
                {/* Progress Percentage */}
                <motion.p
                  className="text-text-muted text-sm mt-2"
                  animate={{ opacity: [0.5, 1, 0.5] }}
                  transition={{ duration: 1.5, repeat: Infinity }}
                >
                  {Math.round(progress)}%
                </motion.p>
              </motion.div>
            )}

            {/* Loading Tips */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 1.5, duration: 0.6 }}
              className="mt-12 space-y-2"
            >
              <p className="text-text-muted text-sm">
                 :        
              </p>
              <div className="flex justify-center gap-4 text-text-muted text-xs">
                <span className="flex items-center gap-1">
                  <TrendingUp size={12} />
                   
                </span>
                <span className="flex items-center gap-1">
                  <Star size={12} />
                   
                </span>
                <span className="flex items-center gap-1">
                  <Zap size={12} />
                   
                </span>
              </div>
            </motion.div>
          </div>
        </div>

        {/* Particles Effect */}
        <div className="absolute inset-0 pointer-events-none">
          {Array.from({ length: 20 }).map((_, i) => (
            <motion.div
              key={i}
              className="absolute w-1 h-1 bg-primary/30 rounded-full"
              style={{
                left: `${Math.random() * 100}%`,
                top: `${Math.random() * 100}%`
              }}
              animate={{
                y: [0, -100, 0],
                opacity: [0, 1, 0],
                scale: [0, 1, 0]
              }}
              transition={{
                duration: 3 + Math.random() * 2,
                repeat: Infinity,
                delay: Math.random() * 2,
                ease: "easeOut"
              }}
            />
          ))}
        </div>
      </motion.div>
    </AnimatePresence>
  )
}

export default LoadingScreen