import { useState, useEffect, useRef } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { Search, X, Gamepad2, Clock } from 'lucide-react'

// Mock search results
const mockGames = [
  {
    id: '1',
    title: 'Cyberpunk 2077',
    genre: ', -',
    price: 299000,
    image: '/imgs/featured_game_1_9.jpg',
    rating: 4.2
  },
  {
    id: '2',
    title: 'The Witcher 3',
    genre: '',
    price: 149000,
    image: '/imgs/featured_game_2_8.jpg',
    rating: 4.8
  },
  {
    id: '3',
    title: 'Starfield',
    genre: '',
    price: 399000,
    image: '/imgs/featured_game_3_9.jpg',
    rating: 4.1
  },
  {
    id: '4',
    title: 'Assassin\'s Creed Valhalla',
    genre: '',
    price: 249000,
    image: '/imgs/featured_game_4_8.jpg',
    rating: 4.3
  }
]

const categories = [
  { id: 'all', name: '', count: 12450 },
  { id: 'action', name: '', count: 2450 },
  { id: 'rpg', name: '', count: 1820 },
  { id: 'strategy', name: '', count: 1150 },
  { id: 'horror', name: '', count: 890 },
  { id: 'racing', name: '', count: 670 },
  { id: 'casual', name: '', count: 3200 }
]

const recentSearches = [
  'Cyberpunk 2077',
  'The Witcher',
  'Assassin\'s Creed',
  'Call of Duty'
]

interface SearchModalProps {
  isOpen: boolean
  onClose: () => void
}

const SearchModal = ({ isOpen, onClose }: SearchModalProps) => {
  const [query, setQuery] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('all')
  const [results, setResults] = useState(mockGames)
  const [recent, setRecent] = useState(recentSearches)
  const [showResults, setShowResults] = useState(false)
  const inputRef = useRef<HTMLInputElement>(null)

  useEffect(() => {
    if (isOpen && inputRef.current) {
      inputRef.current.focus()
    }
  }, [isOpen])

  useEffect(() => {
    const handleEscape = (e: KeyboardEvent) => {
      if (e.key === 'Escape') {
        onClose()
      }
    }

    if (isOpen) {
      document.addEventListener('keydown', handleEscape)
      document.body.style.overflow = 'hidden'
    }

    return () => {
      document.removeEventListener('keydown', handleEscape)
      document.body.style.overflow = 'unset'
    }
  }, [isOpen, onClose])

  const handleSearch = (searchQuery: string) => {
    setQuery(searchQuery)
    
    if (searchQuery.trim()) {
      const filtered = mockGames.filter(game =>
        game.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
        game.genre.toLowerCase().includes(searchQuery.toLowerCase())
      )
      setResults(filtered)
      setShowResults(true)
    } else {
      setResults([])
      setShowResults(false)
    }
  }

  const handleCategoryFilter = (categoryId: string) => {
    setSelectedCategory(categoryId)
    
    if (query.trim()) {
      let filtered = mockGames.filter(game =>
        game.title.toLowerCase().includes(query.toLowerCase()) ||
        game.genre.toLowerCase().includes(query.toLowerCase())
      )
      
      if (categoryId !== 'all') {
        filtered = filtered.filter(game =>
          game.genre.toLowerCase().includes(categoryId)
        )
      }
      
      setResults(filtered)
    }
  }

  const addToRecent = (searchTerm: string) => {
    if (searchTerm && !recent.includes(searchTerm)) {
      setRecent(prev => [searchTerm, ...prev.slice(0, 3)])
    }
  }

  const formatPrice = (price: number) => {
    return new Intl.NumberFormat('fa-IR').format(price) + ' '
  }

  if (!isOpen) return null

  return (
    <AnimatePresence>
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
        className="fixed inset-0 z-50 bg-bg-primary/95 backdrop-blur-sm"
        onClick={onClose}
      >
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -20 }}
          transition={{ duration: 0.3 }}
          className="container mx-auto px-4 pt-20 pb-8"
          onClick={(e: React.MouseEvent) => e.stopPropagation()}
        >
          <div className="max-w-4xl mx-auto">
            
            {/* Search Input */}
            <div className="relative mb-8">
              <div className="relative">
                <Search 
                  size={24} 
                  className="absolute right-4 top-1/2 transform -translate-y-1/2 text-text-muted" 
                />
                <input
                  ref={inputRef}
                  type="text"
                  value={query}
                  onChange={(e) => handleSearch(e.target.value)}
                  placeholder="  ..."
                  className="w-full pr-12 pl-12 py-4 bg-bg-secondary border border-bg-tertiary rounded-xl text-text-primary placeholder-text-muted focus:outline-none focus:border-primary focus:ring-1 focus:ring-primary text-lg"
                />
                <button
                  onClick={onClose}
                  className="absolute left-4 top-1/2 transform -translate-y-1/2 text-text-muted hover:text-text-primary transition-colors"
                >
                  <X size={24} />
                </button>
              </div>
            </div>

            {/* Categories Filter */}
            <div className="flex flex-wrap gap-3 mb-8">
              {categories.map((category) => (
                <motion.button
                  key={category.id}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  onClick={() => handleCategoryFilter(category.id)}
                  className={`px-4 py-2 rounded-full border transition-all ${
                    selectedCategory === category.id
                      ? 'bg-primary text-white border-primary'
                      : 'bg-bg-secondary text-text-secondary border-bg-tertiary hover:border-primary hover:text-text-primary'
                  }`}
                >
                  {category.name}
                  <span className="text-xs opacity-70 mr-1">
                    ({category.count})
                  </span>
                </motion.button>
              ))}
            </div>

            {/* Results */}
            <div className="max-h-96 overflow-y-auto">
              {showResults && query.trim() ? (
                <div className="space-y-3">
                  <h3 className="text-text-primary font-semibold mb-4">
                      ({results.length})
                  </h3>
                  
                  {results.length > 0 ? (
                    <div className="grid gap-3">
                      {results.map((game) => (
                        <motion.div
                          key={game.id}
                          initial={{ opacity: 0, x: -20 }}
                          animate={{ opacity: 1, x: 0 }}
                          whileHover={{ backgroundColor: 'rgba(30, 30, 30, 0.5)' }}
                          className="flex items-center gap-4 p-4 rounded-lg cursor-pointer transition-colors"
                          onClick={() => {
                            addToRecent(query)
                            onClose()
                          }}
                        >
                          <img
                            src={game.image}
                            alt={game.title}
                            className="w-12 h-16 object-cover rounded"
                          />
                          
                          <div className="flex-1">
                            <h4 className="text-text-primary font-medium">
                              {game.title}
                            </h4>
                            <p className="text-text-secondary text-sm">
                              {game.genre}
                            </p>
                            <div className="flex items-center gap-2 mt-1">
                              <span className="text-xs text-text-muted">
                                 {game.rating}
                              </span>
                              <span className="text-sm text-primary font-medium">
                                {formatPrice(game.price)}
                              </span>
                            </div>
                          </div>
                          
                          <Gamepad2 size={20} className="text-text-muted" />
                        </motion.div>
                      ))}
                    </div>
                  ) : (
                    <div className="text-center py-8">
                      <Search size={48} className="text-text-muted mx-auto mb-4" />
                      <p className="text-text-secondary">
                           "{query}"  
                      </p>
                    </div>
                  )}
                </div>
              ) : (
                <div className="space-y-6">
                  {/* Recent Searches */}
                  {recent.length > 0 && (
                    <div>
                      <h3 className="text-text-primary font-semibold mb-4 flex items-center gap-2">
                        <Clock size={18} />
                         
                      </h3>
                      <div className="flex flex-wrap gap-2">
                        {recent.map((search, index) => (
                          <motion.button
                            key={index}
                            whileHover={{ scale: 1.05 }}
                            whileTap={{ scale: 0.95 }}
                            onClick={() => handleSearch(search)}
                            className="px-3 py-2 bg-bg-secondary border border-bg-tertiary rounded-lg text-text-secondary hover:border-primary hover:text-text-primary transition-colors text-sm"
                          >
                            {search}
                          </motion.button>
                        ))}
                      </div>
                    </div>
                  )}

                  {/* Popular Categories */}
                  <div>
                    <h3 className="text-text-primary font-semibold mb-4">
                       
                    </h3>
                    <div className="grid grid-cols-2 md:grid-cols-3 gap-3">
                      {categories.slice(1, 7).map((category) => (
                        <motion.div
                          key={category.id}
                          whileHover={{ scale: 1.02 }}
                          className="p-4 bg-bg-secondary border border-bg-tertiary rounded-lg cursor-pointer hover:border-primary transition-colors text-center"
                          onClick={() => handleCategoryFilter(category.id)}
                        >
                          <h4 className="text-text-primary font-medium mb-1">
                            {category.name}
                          </h4>
                          <p className="text-text-muted text-sm">
                            {category.count.toLocaleString('fa-IR')} 
                          </p>
                        </motion.div>
                      ))}
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  )
}

export default SearchModal