import { create } from 'zustand'
import { persist } from 'zustand/middleware'

export interface Game {
  id: string
  title: string
  genre: string
  price: number
  originalPrice?: number
  discount?: number
  rating: number
  image: string
  platforms: string[]
  developer: string
  description: string
  features: string[]
  systemRequirements: {
    minimum: string
    recommended: string
  }
}

export interface CartItem {
  game: Game
  quantity: number
  addedAt: string
}

interface CartState {
  items: CartItem[]
  isOpen: boolean
  totalItems: number
  totalPrice: number
  discount: number
  
  // Actions
  addItem: (game: Game, quantity?: number) => void
  removeItem: (gameId: string) => void
  updateQuantity: (gameId: string, quantity: number) => void
  clearCart: () => void
  toggleCart: () => void
  openCart: () => void
  closeCart: () => void
  initializeCart: () => void
  calculateTotals: () => void
}

export const useCartStore = create<CartState>()(
  persist(
    (set, get) => ({
      items: [],
      isOpen: false,
      totalItems: 0,
      totalPrice: 0,
      discount: 0,

      addItem: (game: Game, quantity = 1) => {
        const { items } = get()
        const existingItem = items.find((item: CartItem) => item.game.id === game.id)

        let newItems: CartItem[]

        if (existingItem) {
          newItems = items.map((item: CartItem) =>
            item.game.id === game.id
              ? { ...item, quantity: item.quantity + quantity }
              : item
          )
        } else {
          newItems = [
            ...items,
            {
              game,
              quantity,
              addedAt: new Date().toISOString()
            }
          ]
        }

        set({ items: newItems })
        get().calculateTotals()
      },

      removeItem: (gameId: string) => {
        const { items } = get()
        const newItems = items.filter((item: CartItem) => item.game.id !== gameId)
        
        set({ items: newItems })
        get().calculateTotals()
      },

      updateQuantity: (gameId: string, quantity: number) => {
        if (quantity <= 0) {
          get().removeItem(gameId)
          return
        }

        const { items } = get()
        const newItems = items.map((item: CartItem) =>
          item.game.id === gameId
            ? { ...item, quantity }
            : item
        )

        set({ items: newItems })
        get().calculateTotals()
      },

      clearCart: () => {
        set({ 
          items: [], 
          totalItems: 0, 
          totalPrice: 0, 
          discount: 0 
        })
      },

      toggleCart: () => {
        set((state: CartState) => ({ isOpen: !state.isOpen }))
      },

      openCart: () => {
        set({ isOpen: true })
      },

      closeCart: () => {
        set({ isOpen: false })
      },

      initializeCart: () => {
        get().calculateTotals()
      },

      calculateTotals: () => {
        const { items } = get()
        
        const totalItems = items.reduce((sum: number, item: CartItem) => sum + item.quantity, 0)
        
        const totalPrice = items.reduce((sum: number, item: CartItem) => {
          const itemPrice = item.game.price * item.quantity
          return sum + itemPrice
        }, 0)
        
        const discount = items.reduce((sum: number, item: CartItem) => {
          if (item.game.discount && item.game.originalPrice) {
            const itemDiscount = (item.game.originalPrice - item.game.price) * item.quantity
            return sum + itemDiscount
          }
          return sum
        }, 0)

        set({ totalItems, totalPrice, discount })
      },
    }),
    {
      name: 'gamestore-cart',
      partialize: (state: CartState) => ({
        items: state.items,
      }),
    }
  )
)

// Helper functions
export const formatPrice = (price: number): string => {
  return new Intl.NumberFormat('fa-IR').format(price) + ' '
}

export const getCartItemPrice = (item: CartItem): number => {
  return item.game.price * item.quantity
}

export const getCartItemDiscount = (item: CartItem): number => {
  if (item.game.discount && item.game.originalPrice) {
    return (item.game.originalPrice - item.game.price) * item.quantity
  }
  return 0
}